*{
  text-align: center;
}

:root {
  --drum-pad-bg: royalblue;
  --drum-pad-bg-active: rgb(90, 158, 197); 
  --drum-pad-bg-inactive: royalblue; 
}

.drum-pad-container {
  max-width: 75vw;
  margin: 10px auto 10px auto;
}

.drum-pad {
  height: 25vh;
  width: 25vw;
  background-color: var(--drum-pad-bg);
  color: white;
  font-weight: bolder;
}

.drum-pad:hover {
  background-color: #4119e1;
}

.drum-pad:active {
  background-color: var(--drum-pad-bg-active);
}

.pressed-drum {
  background-color: var(--drum-pad-bg-active);
}

.pressed-drum:hover {
  background-color: var(--drum-pad-bg-active);
}

.display {
  height: 50px;
  width: 25vw;
  color: white;
  background-color: tomato;
  margin: 10px auto 10px auto;
  padding: 15px 10px 10px 10px;
  font-weight: bolder;
}